import React, { useState, useEffect } from 'react';
import taco from '../images/taco.png';
import burritos from '../images/burritos.png';
import quesadillas from '../images/quesadillas.png';
import ensalada from '../images/ensalada.png';
import burger from '../images/burger.png';
import limeade from '../images/limeade.png';
import aqua from '../images/aqua.png';
import Close from './Close'




function Menu(){
    const [count, setCount] = useState(0);
    const [newItem, setItem] = useState({
        title: '',
        price: Number
    });
    const [itemArray] = useState([])
    const getT = document.getElementsByClassName('title')
    const getQ = document.getElementsByClassName('qty')
    const getP = document.getElementsByClassName('price')

    //
    const update = (e) => {
        const menubutton = document.getElementsByClassName('menubutton');
        e.preventDefault();
        setCount(count +1);
        setItem({
            title: e.target.name,
            price: e.target.value
        });
        

        
            if (e.target.name == "tacos") {
                menubutton[0].textContent = "Added";
                menubutton[0].style.borderColor = "green";
                menubutton[0].style.transform = "scale(0)";
                menubutton[0].style.visibility = "hidden"
            } else if (e.target.name == "burritos") {
                menubutton[1].textContent = "Added";
                menubutton[1].style.borderColor = "green";
                menubutton[1].style.transform = "scale(0)";
                menubutton[1].style.visibility = "hidden";
            } else if (e.target.name == "quesadillas") {
                menubutton[2].textContent = "Added";
                menubutton[2].style.borderColor = "green";
                menubutton[2].style.transform = "scale(0)";
                menubutton[2].style.visibility = "hidden";
            } else if (e.target.name == "ensalada") {
                menubutton[3].textContent = "Added";
                menubutton[3].style.borderColor = "green";
                menubutton[3].style.transform = "scale(0)";
                menubutton[3].style.visibility = "hidden";
            } else if (e.target.name == "burger") {
                menubutton[4].textContent = "Added";
                menubutton[4].style.borderColor = "green";
                menubutton[4].style.transform = "scale(0)";
                menubutton[4].style.visibility = "hidden";
            }
    }
    

    useEffect(() => {
        const cartDisplay = document.getElementsByClassName('circle')[0];

        cartDisplay.textContent = count;

        itemArray.push(newItem)


        if (itemArray[1] ) {          
            getT[0].textContent = itemArray[1].title;
            getQ[0].style.display = "inline-block"
            getP[0].textContent = itemArray[1].price;
        } if (itemArray[2]) {          
            getT[1].textContent = itemArray[2].title;
            getQ[1].style.display = "inline-block"
            getP[1].textContent = itemArray[2].price;
        } if (itemArray[3]) {          
            getT[2].textContent = itemArray[3].title;
            getQ[2].style.display = "inline-block"
            getP[2].textContent = itemArray[3].price;
        } if (itemArray[4]) {          
            getT[3].textContent = itemArray[4].title;
            getQ[3].style.display = "inline-block"
            getP[3].textContent = itemArray[4].price;
        } if (itemArray[5]) {          
            getT[4].textContent = itemArray[5].title;
            getQ[4].style.display = "inline-block"
            getP[4].textContent = itemArray[5].price;
        }

        setItem({
            title: '',
            price: ''
        })   
    }, [count]);

        
    

    return(
        <div className="menublock">
            <Close />
            <>
            <div className="foods">{food[0].title} <span>&#8358;{food[0].price}</span></div>
            <div className="food-border">
            <img src={taco} className="bounce" style={{"marginTop": "50px", "marginBottom": "30px"}} alt="taco" width="60%"/>
            <p className="food-details">{food[0].details}</p>
            <button className="menubutton" value={food[0].price} name={food[0].title} onClick={update}>Add To Cart</button>
            </div>
            </>
            <>
            <div className="foods">{food[1].title} <span>&#8358;{food[1].price}</span></div>
            <img src={burritos} className="bounce" style={{"marginTop": "50px", "marginBottom": "30px"}} loading="lazy" alt="burritos" width="50%"/>
            <p className="food-details">{food[1].details}</p> 
            <button className="menubutton" value={food[1].price} name={food[1].title} onClick={update}>Add To Cart</button>
            </>
            <>
            <div className="foods">{food[2].title} <span>&#8358;{food[2].price}</span></div>
            <img src={quesadillas} className="bounce" style={{"marginTop": "50px", "marginBottom": "30px"}} loading="lazy" alt="quesadillas" width="50%"/>
            <p className="food-details">{food[2].details}</p> 
            <button className="menubutton" value={food[2].price} name={food[2].title} onClick={update}>Add to Cart</button>
            </>
            <>
            <div className="foods">{food[3].title} <span>&#8358;{food[3].price}</span></div>
            <img src={ensalada} className="round" loading="lazy" alt="ensalada" width="60%"/>
            <p className="food-details">{food[3].details}</p> 
            <button className="menubutton" value={food[3].price} name={food[3].title} onClick={update}>Add to Cart</button>
            </>
            <>
            <div className="foods">{food[4].title} <span>&#8358;{food[4].price}</span></div>
            <img src={burger} className="bounce" loading="lazy" alt="ensalada" width="60%"/>
            <p className="food-details">{food[4].details}</p> 
            <button className="menubutton" value={food[4].price} name={food[4].title} onClick={update}>Add to Cart</button>
            </>
            <>
            <div className="foods">{food[5].title} <span>&#8358;{food[5].price}</span></div>
            <img src={limeade} className="bounce" style={{"marginTop": "50px", "marginBottom": "50px"}} loading="lazy" alt="" width="40%"/>
            <p className="food-details">{food[5].details}</p> 
            <button className="menubutton" value={food[5].price} name={food[5].title} onClick={update}>Unavailable</button>
            </>
            <>
            <div className="foods">{food[6].title} <span>&#8358;{food[6].price}</span></div>
            <img src={aqua} className="bounce" style={{"marginTop": "50px", "marginBottom": "50px"}} loading="lazy" alt="" width="22%"/>
            <p className="food-details">{food[6].details}</p> 
            <button className="menubutton" value={food[6].price} name={food[6].title} onClick={update}>Unavailable</button>
            </>
        </div>
    )
}


const food = [{
    title: 'tacos',
    price: 2500,
    details: 'Your choice of three soft handmade flour tortillas with meat, salsas, and lettuce.'
},
{
    title: 'burritos',
    price: 3000,
    details: 'A large handmade flour tortilla with a choice of lime rice or Mexican red rice, beans, meat, salsa, and cheese.'
},
{
    title: 'quesadillas',
    price: 4000,
    details: 'A large handmade flour tortilla stuffed mozzarella cheese and meat. Served with shredded lettuce, salsa, and fresh guacamole.'
},
{
    title: 'ensalada',
    price: 3000,
    details: 'A large handmade flour tortilla, filled with beans, lime rice or Mexican red rice, meat and lettuce. Topped with salsa, crispy tortilla strips, cheese, and house dressing.'
},
{
    title: 'burger',
    price: 2000,
    details: 'Neque porro quisquam est qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit....'
},
{
    title: 'limeade',
    price: 1000,
    details: 'Lime flavoured drink made with freshly squeezed limes, water and sugar.'
},
{
    title: 'agua de jamaica',
    price: 1000,
    details: 'Fresh hibiscus juice made with pineapple, ginger and bananas.'
}]

export default Menu